.header_img {
    height: 100vh;
    width: 100%;
    background-image: url("./../../assets/image/v4.png");

    /*
    background-image: url("https://images.unsplash.com/photo-1518837695005-2083093ee35b?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1170&q=80");
    */
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}

.title{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 250px  0 150px 0;
    text-align: center;
}

.title h1{
    color: #fff;
    font-size: 80px;
    font-weight: 900;
    margin-bottom: 5px;
}
.title h2{
    color: #fff;
    font-size: 54px;
    font-weight: 900;
    margin: 0;
}

@media screen and (max-width: 768px) {
    .title h1,.title h2 {
        font-size: 40px;
    }
}


@media screen and (max-width: 425px) {
    .title h1,.title h2 {
        font-size: 32px;
    }
}
